import React, {useRef, useEffect} from 'react';
import { Container, Grid, Typography, Stack, Box} from '@mui/material';
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";

import ItemDiv from "./ItemDiv";
import ImageWithSkeleton from './ImageWithSkeleton.js';

gsap.registerPlugin(ScrollTrigger);

const clientStyles = {
    height: {md:120, sm: 150, xs: 210}, 
    width: {md:180, sm: 225, xs: 315}, 
    mt:{md:2, sm: 2, xs: 1},
    display: "flex",
    justifyContent: "center",
    alignItems: "center"
}

const Clients = () => {
    
    const clientRef = useRef(null);
    const tl = useRef(null);
    
    useEffect(() => {
        
        let ctx = gsap.context(() => {

            gsap.set(".firstClients", {opacity:0})
            gsap.set(".secondClients", {opacity:0})
            gsap.set(".thirdClients", {opacity:0})

            tl.current = gsap.timeline({
                scrollTrigger: {
                    trigger: ".firstClients",
                    toggleActions: "restart pause resume none"
                }
            })
            
            tl.current.to(".firstClients", {opacity: 1, duration: 3})
            tl.current.to(".secondClients", {opacity: 1, duration: 3, delay: -2.7})
            tl.current.to(".thirdClients", {opacity: 1, duration: 3, delay: -2.7})

        }, clientRef);

        return () => ctx.revert();

    }, []);

    return (
        <Container component="section" ref={clientRef} sx={{pt:38}} maxWidth={"lg"} fixed>
            <Grid container spacing={3}>
                <Grid item xs={12}>
                    <Typography variant="h3" component="h4" align="center">Clients</Typography>
                </Grid>

                <Grid container direction="row" justifyContent="center" alignItems="center" item xs={12}>
                    <ItemDiv sx={{height:3, width:40, padding:0}} darkhex="#AE7EAA" lighthex="#ff97cc"/>
                </Grid>

                <Grid item xs={12}>
                    <Stack direction="row" justifyContent="center" alignItems="center" spacing={{md:2, sm: 2, xs: 1}}>
                        <Box className="thirdClients" sx={{display: {md:"inline", sm: "none", xs: "none"}}}>
                            <ItemDiv sx={clientStyles}>
                                <ImageWithSkeleton source={"/images/GLI.png"} text={"GLI Logo"} width={100} height={120}/>
                            </ItemDiv>
                        </Box>
                        <Box className="secondClients" sx={{display: {md:"inline", sm: "inline", xs: "none"}}}>
                            <ItemDiv sx={clientStyles}>
                                <ImageWithSkeleton source={"/images/NTH.png"} text={"Neon Treehouse Logo"} width={100} height={50}/>
                            </ItemDiv>
                            <ItemDiv sx={clientStyles}>
                                
                                <ImageWithSkeleton source={"/images/tgin.png"} text={"Blind Tiger Logo"} width={100} height={50}/>
                            </ItemDiv>
                        </Box>
                        <Box className="firstClients">
                            <ItemDiv sx={clientStyles}>
                                <ImageWithSkeleton source={"/images/LINK4.png"} text={"Link4 Logo"} width={100} height={50}/>
                            </ItemDiv>
                            <ItemDiv sx={clientStyles}>
                                <ImageWithSkeleton source={"/images/Karmine.webp"} text={"Karmine Leather Logo"} width={100} height={50}/>
                            </ItemDiv>
                            <ItemDiv sx={clientStyles}>
                                
                                <ImageWithSkeleton source={"/images/aflmax.svg"} text={"AFL Max Logo"} width={100} height={50}/>
                            </ItemDiv>
                        </Box>
                        <Box className="secondClients" sx={{display: {md:"inline", sm: "inline", xs: "none"}}}>
                            <ItemDiv sx={clientStyles}>
                                <ImageWithSkeleton source={"/images/Vocab.png"} text={"Vocablyfy Logo"} width={100} height={50}/>
                            </ItemDiv>
                            <ItemDiv sx={clientStyles}>
                                <ImageWithSkeleton source={"/images/gmeats.png"} text={"GDay Meats Logo"} width={100} height={50}/>
                            </ItemDiv>
                        </Box>
                        <Box className="thirdClients" sx={{display: {md:"inline", sm: "none", xs: "none"}}}>
                            <ItemDiv sx={clientStyles}>
                                <ImageWithSkeleton source={"/images/WBG.png"} text={"WBG Legal Logo"} width={100} height={50}/>
                            </ItemDiv>
                        </Box>
                    </Stack>
                </Grid>
            </Grid>
        </Container>
    )
}

export default Clients