import React from 'react';
import Skeleton from '@mui/material/Skeleton';

const ImageWithSkeleton = ({source, text, width, height}) => {
    const [loaded, setLoaded] = React.useState(false);
    return(
        <>
            {loaded ? null : (<Skeleton variant="rounded" animation="wave" width={width} height={height}/>)}
            <img
                style={loaded ? {width:"60%"} : { display:"none"}}
                src={source}
                alt={text}
                onLoad={() => {setLoaded(true)}}
            />      
        </>
    )
}

export default ImageWithSkeleton